$main:#9fffff55;
$cyan: #61dafbaf;
$gray:#31343E;
$veryGray:#20232A;
a {
  color: unset;
  text-decoration: unset;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: $main;
  
}
.overflow{
  overflow: hidden;
}

header {
  background-color: $veryGray;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  color: #61dafb;
  text-shadow:1px 1px 1px #111111,
              -1px -1px 1px #111111,
              2px 2px 1px #111111,
              -2px -2px 1px #111111;
  margin-bottom: 30px;

  h2 {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  ul {
    display: flex;
    align-items: center;
    gap: 15px;
    font-weight: 600;
    font-size: 1.2rem;
  }
  .active{
    background-color: $gray;
    padding: .6em 1.2em;
    border-radius: 10px;
  }
}

.items-container{
  display: grid;
  grid-template-columns: repeat(auto-fill , minmax(270px , 1fr));
  gap: 25px;
  align-items: center;
  // display: flex;
  // flex-flow: row wrap;
  padding: 1em;
  .card {
    box-shadow: 0 0 7px #20232A41;
    max-width: 550px;
    min-width: 100%;
    margin: auto;
    border-radius: 10px;
    background:linear-gradient( $veryGray, $gray);
    color: #dfdfdf;
    height: 100%;
    text-align: center;
    display: flex;
    flex-flow: column;
    .disc{
      padding: .7rem;
      display: flex;
      flex-flow: column;
      justify-content: space-between;
      gap: .5rem;
      height: 50%;
      p {
        font-size: 1rem;
      }
    }
    .buttons {
      display: flex;
      flex-flow: row nowrap;
      gap: 1rem;
      justify-content: center;
    }
    .rate {
      font-size: 1.7rem;
      font-weight: 700;
      svg {
        margin-right: .5rem;
      }
    }
    button {
      background-color: #EE7754;
      border: none;
      color: #f0f0f0;
      padding: .3em .5em;
      border-radius: 7px;
      cursor: pointer;
      font-size: 1.8rem;
      font-weight: 600;
      margin-top: 1.5rem;
    }
  }
  .img-container {
    min-height: 250px;
    max-height: 380px;
    width: 100%;
    background-position: 50% 50%;
    background-size: cover;
    transition: transform .3s;
    background-repeat: no-repeat;
    border-radius: 10px;
  }
  .img-container {
     &:hover {
      transform:scale(1.15) rotate(5deg) ;
    }
  }
}
.App {
  min-height: 100vh;
  display: flex;
  flex-flow: column ;
}
footer {
  // position: absolute;
  text-align: center;
  width: 100%;
  background-color: $veryGray;
  color: white;
  padding: 1rem;
  font-size: 1.2rem;
  margin-top:auto;
  h3 {font-weight: 400; letter-spacing: 1rem;}
}
.add {
  display: flex;
  flex-flow: column;
  text-align: center;
  font-size: 1.5rem;
  .form {
    text-align: center;
    background-color: $gray;
    border-radius: 20px;
    padding: 1.5rem;
    @extend .add;
    margin: auto;
    min-width: calc(100% - 30vw);
    min-height: 100%;
    gap: 50px;
    color: white;
    label {
      display: flex;
      flex-direction: column;
      gap: 15px;
      // margin-bottom: -15px;
      
      h1 {
        margin: 0;

      }
    }
    input , textarea , select {border:none; padding: .5rem; resize: vertical; margin-bottom: -15px;}
  }
  @media (min-width:950px) {
    .form {
      min-width:650px;
    }
  }
}
.btn {
  padding: 1rem;
  color: white;
  background-color: $cyan;
  border: none;
  border-radius: 5px;
  font-size: 2rem;
  transition: .1s;
  &:hover {
    background-color: $main;
    
  }
}

.hide {
  display: none;
}
@keyframes popup {
  from {
    transform: scale(0);
  }
  to {
    // transform-origin: top center;
    transform: scale(1);
  }
}
.overlay {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  background-color: #111111a3;
  width: 100%;
  height: 100%;
  z-index: 100;
  padding: 1rem;
  .popup {
    position: relative;
    // top: 50%;
    // left: 50%;
    // z-index: 99;
    background-color: $veryGray;
    padding: .5rem;
    color: white;
    width:calc(100% - 15px) ;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 20px;
    border-radius: 7px;
    animation: popup both .3s;
    transition: 1s;
    input {
      padding: .3rem;
      outline: none;
    }
    textarea {
      resize: vertical;
      padding: .5rem;
      font-size: 1rem;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-weight: 600;
    }
    .rate {
      display: flex;
      flex-direction: column;
    }
    button {
      background-color: #EE7754;
      border: none;
      color: #f0f0f0;
      padding: .3em .5em;
      border-radius: 7px;
      cursor: pointer;
      font-size: 1.8rem;
      font-weight: 600;
    }
    .x {
      position: absolute;
      font-size: 2rem;
      right: .2rem;
      top: .2rem;
      padding: .5rem;
      color: #EE7754;      
      cursor: pointer;
    }
  }
}

@media(max-width:500px) {
  html {
    font-size: 12px;
  }
}